import { Injectable } from '@angular/core';
import { ToastModel } from '@core/models/toast.model';


@Injectable({
  providedIn: 'root'
})
export class ToastService {
  constructor() { }

  toasts: ToastModel[] = [];

  show(toast: ToastModel) {
    this.toasts.push(toast);
  }

  remove(toast: ToastModel) {
    this.toasts = this.toasts.filter(t => t !== toast);
  }

  success(msg: string) {
    this.toasts.push({
      class: 'success',
      textOrTpl: msg
    });
  }

  error(msg: string) {
    this.toasts.push({
      class: 'danger',
      textOrTpl: msg
    });
  }

}
