import { Component, OnInit } from '@angular/core';
import { EnvironmentApplication } from '@shared/constants';
import { LoginService } from 'src/app/core/services/login.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'duelit-core-container',
  templateUrl: './core-container.component.html',
  styleUrls: ['./core-container.component.scss']
})
export class CoreContainerComponent implements OnInit {

  constructor(
    public loginService: LoginService
  ) { }

  ngOnInit(): void {
  }

  get isAuthenticated(): boolean {
    return this.loginService.isAuthenticated;
  }

  doLogout() {
    this.loginService.logout().subscribe(() => { });
  }

  onLogoClick() {
    this.loginService.navigateToHomePage();
  }

  isRedeem() {
    return environment.application === EnvironmentApplication.Redeem;
  }
}
