import { Component, EventEmitter, HostBinding, HostListener, Input, OnInit, Output } from '@angular/core';
import { faBars } from '@fortawesome/free-solid-svg-icons';
import { EnvironmentApplication, EnviromentApi } from '@shared/constants';
import { environment } from 'src/environments/environment';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ChangeEnvModalComponent } from '../change-env-modal/change-env-modal.component';
import { LoginService } from '@core/services/login.service';
import { ApiService } from '@core/services/api.service';

@Component({
  selector: 'duelit-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
  @Input() authenticated = false;
  @Input() firstName: string;
  @Output() readonly logout = new EventEmitter();
  @Output() readonly logoClicked = new EventEmitter();
  private _innerWidth: number;
  menu = faBars;
  // isMenuOpened = false;
  readonly WIDE_SCREEN_LIMIT = 768;

  _navList = [{
    name: 'Redeem',
    path: ['/redeem'],
    env: EnvironmentApplication.Redeem
  }, {
    name: 'Dashboard',
    path: ['/admin/dashboard'],
    env: EnvironmentApplication.Admin
  }, {
    name: 'Users',
    path: ['/admin/users'],
    env: EnvironmentApplication.Admin
  }, {
    name: 'Tournaments',
    path: ['/admin/tournaments'],
    env: EnvironmentApplication.Admin
  }, {
    name: 'Countries',
    path: ['/admin/countries'],
    env: EnvironmentApplication.Admin
  }];

  get navList() {
    if (environment.application === EnvironmentApplication.Dev) {
      return this._navList;
    }
    return this._navList.filter(nav => nav.env === environment.application);
  }

  get innerWidth(): number {
    return this._innerWidth;
  }
  set innerWidth(width) {
    if (width >= this.WIDE_SCREEN_LIMIT) {
      this.isMenuOpened = false;
    }
    this._innerWidth = width;
  }
  @HostBinding('class.opened-menu') isMenuOpened: boolean;

  @HostListener('window:resize', ['$event'])
  onResize($event: number) {
    this.innerWidth = window.innerWidth;
  }

  constructor(
    private modalService: NgbModal,
    private loginService: LoginService,
    private api: ApiService
  ) {
    this.isMenuOpened = false;
  }

  ngOnInit(): void {
    this.innerWidth = window.innerWidth;
  }

  doLogout() {
    this.logout.emit();
  }

  isEnvironmentAdmin(): boolean {
    return environment.application === EnvironmentApplication.Admin || environment.application === EnvironmentApplication.Dev;
  }

  openChangeEnv() {
    const modal = this.modalService.open(ChangeEnvModalComponent);

    modal.result.then(({ action, environment: env }) => {
      if (action === 'ok' && env !== this.api.selectedEnvApi) {
        if (this.loginService.isAuthenticated) {
          this.loginService.logout().subscribe(() => {
            this.api.selectedEnvApi = env;
          });
        } else {
          this.api.selectedEnvApi = env;
        }
      }
    }).catch((err) => {
      console.log(err);
      return;
    });
  }

  isEnvirontmentApiProd(): boolean {
    return this.api.selectedEnvApi === EnviromentApi.Prod;
  }

  getSelectedEnv(): EnviromentApi {
    return this.api.selectedEnvApi;
  }
}
