import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatMenuModule } from '@angular/material/menu';
import { RouterModule } from '@angular/router';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';

import { CoreContainerComponent } from './core-container.component';
import { FooterComponent } from './footer/footer.component';
import { HeaderComponent } from './header/header.component';
import { ChangeEnvModalComponent } from './change-env-modal/change-env-modal.component';
import { NgbModalModule, NgbDropdownModule } from '@ng-bootstrap/ng-bootstrap';


@NgModule({
  declarations: [CoreContainerComponent, HeaderComponent, FooterComponent, ChangeEnvModalComponent],
  imports: [
    CommonModule,
    MatMenuModule,
    FontAwesomeModule,
    RouterModule,
    NgbModalModule,
    NgbDropdownModule,
  ],
  exports: [CoreContainerComponent]
})
export class CoreContainerModule { }
