import { Injectable, NgZone } from '@angular/core';
import { Router, UrlTree } from '@angular/router';
import { of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';
import { EnvironmentApplication } from 'src/app/shared/constants';
import { UsersApi } from 'src/app/shared/constants/api';
import { UserRole } from 'src/app/core/constants/user-role.enum';
import { environment } from 'src/environments/environment';

import { UserProfileModel } from '../models/user-profile.model';
import { ApiService } from './api.service';

@Injectable()
export class LoginService {
  isAuthenticated = false;
  // _token: string;
  readonly DUELIT_TOKEN = 'duelitToken';
  readonly DUELIT_ROLE = 'duelitRole';
  user: UserProfileModel;


  constructor(
    private router: Router,
    private api: ApiService,
    private zone: NgZone
  ) { }

  get token(): string {
    return localStorage.getItem(this.DUELIT_TOKEN);
  }

  set token(token: string) {
    if (token) {
      localStorage.setItem(this.DUELIT_TOKEN, token);
    } else {
      localStorage.removeItem(this.DUELIT_TOKEN);
      localStorage.removeItem(this.DUELIT_ROLE);
    }
  }

  Init(): Promise<boolean> {
    if (!this.token) {
      this.isAuthenticated = false;
      return Promise.resolve(false);
    }
    return this.api.get(UsersApi.MyProfile).pipe(
      map((result: UserProfileModel) => {
        this.isAuthenticated = !!result.email;
        this.user = {
          ...result,
          role: localStorage.getItem(this.DUELIT_ROLE) as UserRole,
          token: this.token
        };
        return !!result.email;
      }),
      catchError(err => {
        this.isAuthenticated = false;
        return of(false);
      }),
      tap(isAuthenticated => {
        if (!isAuthenticated) {
          this.router.navigate(['/login']);
        }
      })
    ).toPromise();
  }

  login(email: string, password: string) {
    return this.api.post(UsersApi.Login, { body: { email, password , country: 'United States' } }).pipe(
      map((user: UserProfileModel) => {
        if (!user) {
          this.isAuthenticated = false;
          return false;
        }
        this.user = user;
        this.token = user.token;
        localStorage.setItem(this.DUELIT_ROLE, user.role);
        this.isAuthenticated = true;
        this.router.navigate(this.getUrlForLoggedUser());
        return user;
      }),
      catchError(err => of(false)),
    );
  }

  getUrlForLoggedUser() {
    return environment.application === EnvironmentApplication.Redeem ? ['/redeem'] : ['/admin'];
  }

  urlTreeForLoggedUser(): UrlTree {
    return this.router.createUrlTree(this.getUrlForLoggedUser());
  }

  navigateToHomePage() {
    this.router.navigate(this.getUrlForLoggedUser());
  }

  setToken(token) {
    localStorage.setItem(this.DUELIT_TOKEN, token);
  }

  logout() {
    return this.api.post(UsersApi.Logout, {
      body: { device_token: 'none' },
      opts: { addSecurity: false }
    }).pipe(
      map(() => {
        localStorage.removeItem(this.DUELIT_TOKEN);
        localStorage.removeItem(this.DUELIT_ROLE);
        this.router.navigate(['/login']);
        this.isAuthenticated = false;
        this.user = null;
        this.token = null;
        return of({ });
      })
    );
  }
}
