import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { EnviromentApi } from '@shared/constants';
import { ApiSecurity, ApiConfig } from 'src/environments/environment.interface';

const DEFAULT_API_DETAILS = {
  query: {},
  opts: {
    addSecurity: true
  }
};

export const SELECTED_ENV = 'selectedEnvironment';

@Injectable({
  providedIn: 'root'
})
export class ApiService {
  private _selectedEnvApi: EnviromentApi;
  selectedApiConfig: ApiConfig;

  constructor(private http: HttpClient) { }

  set selectedEnvApi(value: EnviromentApi) {
    this._selectedEnvApi = value;
    localStorage.setItem(SELECTED_ENV, value);
    this.selectedApiConfig = environment.api.find(config => config.environment === value);
  }
  get selectedEnvApi(): EnviromentApi {
    return this._selectedEnvApi;
  }

  get baseuri(): string {
    return this.selectedApiConfig.host;
  }

  get security(): ApiSecurity {
    return this.selectedApiConfig.security;
  }

  get(path: string, details: ApiDetails = {
    query: {}
  }) {

    return this.http.get(this.buildRequestUrl(path, details.query));
  }

  delete(path: string, details: ApiDetails = {
    query: {}
  }) {

    return this.http.delete(this.buildRequestUrl(path, details.query));
  }

  post(path: string, details: ApiDetails = {
    query: {}
  }) {
    const opts = {
      ...DEFAULT_API_DETAILS.opts,
      ...details.opts
    };

    const body = {
      ...details.body
    };

    if (opts.addSecurity) {
      // tslint:disable-next-line: no-string-literal
      body['custom_uid'] = this.security.custom_uid;
      // tslint:disable-next-line: no-string-literal
      body['device_type'] = this.security.device_type;
      // tslint:disable-next-line: no-string-literal
      body['device_token'] = this.security.device_token;
    }


    return this.http.post(this.buildRequestUrl(path, details.query), body);
  }

  put(path: string, details: ApiDetails = {
    query: {}
  }) {
    const opts = {
      ...DEFAULT_API_DETAILS.opts,
      ...details.opts
    };

    const body = {
      ...details.body
    };

    if (opts.addSecurity) {
      // tslint:disable-next-line: no-string-literal
      body['custom_uid'] = this.security.custom_uid;
      // tslint:disable-next-line: no-string-literal
      body['device_type'] = this.security.device_type;
      // tslint:disable-next-line: no-string-literal
      body['device_token'] = this.security.device_token;
    }

    return this.http.put(this.buildRequestUrl(path, details.query), body);
  }

  private buildRequestUrl (path: string, query: HttpQuery = {}): string {
    const queries = Object.keys(query).map(key => {
      return `${key}=${query[key]}`;
    });

    return `${this.baseuri}${path}${queries.length > 0 ? '?' : ''}${queries.join('&')}`;
  }

  buildPath(path: string, params: (string|number)[] | { [param: string]: string|number } = []): string {
    let _path = path;

    if (Array.isArray(params)) {
      params.forEach(param => _path = _path.replace(/:.*?:/, `${param}`));
    } else {
      Object.entries(params).forEach(([param, value]) => {
        _path = _path.replace(`:${param}:`, `${value}`);
      });
    }

    return _path;
  }
}

export interface ApiDetails {
  query?: HttpQuery;
  body?: object | Array<any>;
  opts?: {
    addSecurity: boolean;
  };
}

export interface HttpQuery { [ key: string ]: string | number; }
