import { ApiService, SELECTED_ENV } from './core/services/api.service';
import { EnviromentApi, isEvironmentApiValid } from '@shared/constants';
import { environment } from 'src/environments/environment';
import { Provider, APP_INITIALIZER } from '@angular/core';
import { LoginService } from '@core/services/login.service';

function initSelectedEnvironment(api: ApiService) {
  return () => {
    const preSelected = localStorage.getItem(SELECTED_ENV);
    const defaultApiEnv = environment.api.find(apiEnv => apiEnv.default);
    const env: EnviromentApi = isEvironmentApiValid(preSelected) &&
      environment.api.findIndex(apiEnv => apiEnv.environment === preSelected) >= 0 ? preSelected as EnviromentApi :
      defaultApiEnv ? defaultApiEnv.environment : EnviromentApi.Local;

    api.selectedEnvApi = env;
  };
}

const initUser = (loginService: LoginService) => {
  return () => loginService.Init();
};

export const INIT_API_CONFIG: Provider = {
  provide: APP_INITIALIZER,
  useFactory: initSelectedEnvironment,
  deps: [ApiService],
  multi: true
};

export const INIT_USER: Provider = {
  provide: APP_INITIALIZER,
  useFactory: initUser,
  multi: true,
  deps: [LoginService]
};
