import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdminGuard } from '@guards/admin.guard';

import { RedeemGuard } from './guards/redeem.guard';


const routes: Routes = [
  { path: 'redeem', loadChildren: () => import('./features/redeem/redeem.module').then(m => m.RedeemModule),
    canActivate: [ RedeemGuard ],
  },
  {
    path: 'paypal/user-data',
    loadChildren: () => import('./features/paypal/paypal.module').then(m => m.PaypalModule),
    canActivate: [ RedeemGuard ]
  },
  { path: 'admin', loadChildren: () => import('./features/admin/admin.module').then(m => m.AdminModule),
    canActivate: [ AdminGuard ],
  },
  { path: '', loadChildren: () => import('./core/core.module').then(m => m.CoreModule) },
  { path: '**', pathMatch: 'full', redirectTo: '/login' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
